import { HttpClient } from '@farris/mobile-devkit';
import { FrameworkSessionService } from '../../framework_session_service';
import { BeSessionStorageStrategy, SessionStorageBeSessionStorageStrategy } from '../storage-strategies/index';
import { BefSessionHandlingStrategy } from './handling_strategy';
import { BefSeparatedSessionHandlingStrategy } from './separated_handling_strategy';
import { BefUnifiedSessionHandlingStrategy } from './unified_handling_strategy';


/**
 * BeSession
 */
class BefSessionHandlingStrategyFactory {

  /**
   * BeSession
   */
  public create(
    handlingStrategyName: string,
    frmSessionService: FrameworkSessionService,
    beBaseUrl: string,
    httpClient: HttpClient
  ): BefSessionHandlingStrategy {

    const storageStrategy = this.createStorageStrategy();

    if (handlingStrategyName === 'UnifiedSession') {
      return new BefUnifiedSessionHandlingStrategy(storageStrategy, frmSessionService, httpClient, beBaseUrl);
    } else {
      return new BefSeparatedSessionHandlingStrategy(storageStrategy, frmSessionService, httpClient, beBaseUrl);
    }
  }

  /**
   * BeSession
   */
  private createStorageStrategy(): BeSessionStorageStrategy {
    return new SessionStorageBeSessionStorageStrategy();
  }

}

export { BefSessionHandlingStrategyFactory };


