import { Observable, of, EMPTY, throwError } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { Entity, HttpHeaders, InjectFlags, Injector } from '@farris/mobile-devkit';
import { IErrorServe, ResponseInfo } from './types';
import { BefRepository } from './bef_repository';
import { RequestInfoUtil } from './utils';

import { BE_ERROR_HANDLER__TOKEN } from './tokens';

/**
 * 
 */
export interface IProxyExtend {
  extendBody(body: any): { [propName: string]: any };
  extendHeaders(headers: { [propName: string]: any }): Observable<any>;
  onResponse?(response: ResponseInfo, ignoreChanges?: boolean);
  onError?(error: any, selfHandError: boolean, ignoreError: boolean): Observable<any>;
}

/**
 * Bef
 */
export class BefProxyExtend implements IProxyExtend {

  /**
 * 
 */
  private get injector() {
    return this.repository.getInjector();
  }

  /**
   * 
   */
  constructor(private repository: BefRepository<Entity>) { }

  /**
   * 
   */
  public onResponse(response: any): ResponseInfo {
    this.handleResponseHeaders(response.headers);
    return this.handleResponseBody(response.body);
  }

  /**
   * headers
   */
  private handleResponseHeaders(headers: any): void {
    this.repository.sessionService.handleResponseHeaders(headers);
  }

  /**
   * body
   */
  private handleResponseBody(responseInfo: ResponseInfo): ResponseInfo {
    if (responseInfo && responseInfo.innerDataChange) {
      this.repository.entityManager.handleDataChangeDetails(responseInfo.innerDataChange);
    }
    this.repository.entityManager.clearAllEntityChanges();

    if (responseInfo && responseInfo.hasOwnProperty('returnValue')) {
      return responseInfo.returnValue;
    } else {
      return responseInfo;
    }
  }

  /**
   * 
   */
  public onError(error: any): Observable<any> {
    // load
    const loadingServices = window['DEVKIT_LOADING_SERVICE'];
    if (loadingServices && loadingServices instanceof Array && loadingServices.length > 0) {
      for (const loadingService of loadingServices) {
        if (typeof (loadingService.hide) === 'function') {
          loadingService.hide();
        }
      }
    }
    // 
    const exceptionService = this.injector.get<IErrorServe>(BE_ERROR_HANDLER__TOKEN, null)
    if (!!exceptionService) {
      exceptionService.show(error)
      return EMPTY;
    } else {
      return EMPTY;
    }
  }

  /**
   * Headers
   */
  public extendHeaders(headers: HttpHeaders): Observable<{ [propName: string]: any }> {
    const $getSessionId = this.repository.sessionService.getBeSessionId();
    return $getSessionId.pipe(
      switchMap(() => {
        headers = this.repository.sessionService.extendRequestHeaders(headers);
        return of(headers);
      })
    );
  }

  /**
   * Body
   */
  public extendBody(body: any): any {
    return RequestInfoUtil.appendRequestInfoToBody(body, this.repository);
  }

}
