import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Entity, HttpMethods, HttpRequestConfig, ViewModelContext } from '@farris/mobile-devkit';
import { ResponseInfo } from './types';
import { RequestInfoUtil } from './utils/index';
import { BefRepository } from './bef_repository';

/**
 * Bef
 */
export class BefLookupDataService {

  private viewModelContext: ViewModelContext;

  private befRepository: BefRepository<Entity>;

  public context: any;

  /**
   * 
   */
  constructor(viewModelContext: ViewModelContext) {
    this.viewModelContext = viewModelContext;
    this.befRepository = this.viewModelContext.repository as BefRepository<Entity>
  }

  /**
   * 
   */
  public getData(helpMetadataId: string, queryParam?: any): Observable<any> {
    const tableName = helpMetadataId.split('.')[0];
    const labelId = helpMetadataId.split('.')[1];
    queryParam = queryParam || {};
    return this.extendGetHelpData(labelId, tableName, queryParam);
  }

  /**
   * 
   */
  private extendGetHelpData(labelId: string, tableName: string, queryParam: any): Observable<any> {
    const url = `${this.befRepository.apiProxy.baseUrl}/extension/elementhelps`;
    const requestInfo = RequestInfoUtil.buildRequestInfo(this.befRepository);
    const body = {
      labelId: labelId,
      nodeCode: tableName,
      queryParam: queryParam,
      requestInfo: requestInfo
    };
    const requestConfig: HttpRequestConfig = {
      body: body
    };

    const result$ = this.befRepository.apiProxy.request(HttpMethods.PUT, url, requestConfig);
    return result$.pipe(
      map((result: any) => {
        return result;
      })
    );
  }
}