import { Observable } from 'rxjs';
import { BefSessionHandlingStrategy } from './handling-strategies/index';

/**
 * BefSessionService
 */
class BefSessionService {

  /**
   * Session
   */
  private handlingStrategy: BefSessionHandlingStrategy;

  /**
   * 
   */
  constructor(handlingStrategy: BefSessionHandlingStrategy) {
    this.handlingStrategy = handlingStrategy;
  }
  /**
   * token
   */
  public get token() {
    return this.handlingStrategy.getFrameworkSessionId();
  }

  /**
   * BeSessionId
   */
  public getBeSessionId(): Observable<string> {
    return this.handlingStrategy.getSessionId();
  }

  /**
   * BeSession
   */
  public getBeSessionExisted(): Observable<boolean> {
    return this.handlingStrategy.getBeSessionExisted();
  }

  /**
   * sessionId
   * @param sessionId sessionId
   */
  public setBeSessionId(sessionId: string) {
    this.handlingStrategy.setSessionId(sessionId);
  }

  /**
   * BeSessionId
   */
  public clearBeSessionId() {
    this.handlingStrategy.clearSessionId();
  }

  /**
   * header
   */
  public extendRequestHeaders(headers: any): any {
    return this.handlingStrategy.handleRequestHeaders(headers);
  }

  /**
   * header
   */
  public handleResponseHeaders(headers: any): void {
    return this.handlingStrategy.handleReponseHeaders(headers);
  }
}

export { BefSessionService };
