import { Type, makeDecorator, makePropDecorator } from '@farris/mobile-devkit';


/**
 * 
 */
export const VARIABLE_PROP_META = '@farris/bef VARIABLE_PROP_META';


/**
 * NgVariable
 */
export interface VariablePropMetadata {

  /**
   * 
   */
  mapping: string;

}


/**
 * NgVariable
 */
export interface VariablePropDecorator {
  (obj?: VariablePropMetadata): any;
  new(obj?: VariablePropMetadata): any;
}


/**
 * NgVariable
 */
export const VariablePropMeta: VariablePropDecorator =
  makePropDecorator(VARIABLE_PROP_META, (obj: VariablePropMetadata) => obj);
