import { Observable, of, zip, EMPTY } from 'rxjs';
import { tap, map, switchMap } from 'rxjs/operators';
import { Entity, DataChangeType, } from '@farris/mobile-devkit';

import { QueryResult } from './types';
import { BefRepository } from './bef_repository';
import { BefProxy } from './bef_proxy';

/**
 * Bef
 */
class BefDataService<T extends Entity> {

  /**
   * 
   */
  private repository: BefRepository<T>;

  /**
   * Api
   */
  private get apiProxy(): BefProxy {
    return this.repository.apiProxy;
  }

  /**
   * 
   */
  constructor(repository: BefRepository<T>) {
    this.repository = repository;
  }

  /**
   * 
   */
  public getList(filter: any[], sort: any[], pageSize: number | null, pageIndex: number | null): Observable<T[]> {
    const entityFilter = this.buildEntityFilter(filter, sort, pageSize, pageIndex);
    const query$ = this.apiProxy.extendQuery(entityFilter);
    const result$ = query$.pipe(
      map((returnValue: any) => {
        this.repository.setPaginationInfo((returnValue as QueryResult).pagination)
        const listData = (returnValue as QueryResult).result;
        const entities = this.repository.buildEntities(listData);
        if (this.determineIsAppend(pageIndex)) {
          this.repository.entityCollection.addEntities(entities);
        } else {
          this.repository.entityCollection.loadEntities(entities);
        }
        return entities;
      })
    );

    return result$;
  }

  /**
   * 
   */
  determineIsAppend(pageIndex: number): boolean {
    return pageIndex > 1
  }

  /**
   * 
   */
  public getById(id: string): Observable<T> {
    const retrieve$ = this.apiProxy.extendRetrieve(id);
    const result$ = retrieve$.pipe(
      map((returnValue: any) => {
        const entityData = returnValue;
        const entity = this.repository.buildEntity(entityData);
        this.repository.entityCollection.loadEntities([entity]);
        return entity;
      })
    );

    return result$;
  }

  /**
   * 
   */
  public editById(id: string): Observable<T> {
    const entity = this.repository.entityCollection.getEntityById(id);
    if (!entity) {
      return of(null);
    }

    const retrieve$ = this.apiProxy.edit(id);
    const result$ = retrieve$.pipe(
      map((returnValue: any) => {
        const entityData = returnValue.data;
        const entityToUpdate = this.repository.entityCollection.getEntityById(id);
        if (entityToUpdate && entityData) {
          this.reloadEntityData(entityToUpdate, entityData);
        }
        return entityToUpdate;
      })
    );

    return result$;
  }

  /**
   * id
   * @param id id
   */
  public updateById(id: string): Observable<T> {
    const entity = this.repository.entityCollection.getEntityById(id);
    if (!entity) {
      return EMPTY;
    }

    const retrieve$ = this.apiProxy.extendRetrieve(id);
    const result$ = retrieve$.pipe(
      map((returnValue: any) => {
        const entityData = returnValue;
        const entityToUpdate = this.repository.entityCollection.getEntityById(id);
        this.reloadEntityData(entityToUpdate, entityData);
        return entityToUpdate;
      })
    );

    return result$;
  }

  /**
   * 
   */
  private reloadEntityData(entity: Entity, entityData: any): void {
    if (!entity) {
      return;
    }
    entity.load(entityData);
    entity.changes.splice(0, entity.changes.length);
  }

  /**
   * 
   */
  public create(defaultValue?: any) {
    const result$ = this.apiProxy.create(defaultValue);
    return result$.pipe(
      map((returnValue: any) => {
        const newEntityData = returnValue;
        const newEntity = this.repository.buildEntity(newEntityData);
        this.repository.entityCollection.loadEntities([newEntity]);
        this.repository.dataChangeHistory.addChange({ dataId: newEntity.primaryValue, changeType: DataChangeType.Add });
        return newEntity;
      })
    );
  }

  /**
   * 
   */
  public append(defaultValue?: any): Observable<T> {
    const append$ = this.apiProxy.create(defaultValue);
    return append$.pipe(
      map((returnValue: any) => {
        const newEntityData = returnValue;
        const newEntity = this.repository.buildEntity(newEntityData);
        this.repository.entityCollection.addEntity(newEntity);
        return newEntity;
      })
    );
  }

  /**
   * 
   * @param fpath /1/edus/1/edus/2/grades
   */
  public appendByPath(fpath: string): Observable<Entity> {
    const append$ = this.apiProxy.createByPath(fpath);
    return append$.pipe(
      map((returnValue: any) => {
        const newEntityData = returnValue;
        const newEntity = this.repository.entityManager.appendEntityByPath(fpath, newEntityData, newEntityData);
        return newEntity;
      })
    );
  }


  /**
   * id
   * @param id 
   */
  public removeById(id: string, ifSave?: boolean): Observable<boolean> {

    ifSave = (ifSave === undefined) ? true : ifSave;
    const delete$ = this.apiProxy.extendDelete(id);

    // 
    return delete$.pipe(
      switchMap(() => {
        // 
        if (ifSave === true) {
          return this.applyChangesById(id).pipe(tap(result => {
            if (result) {
              this.repository.entityCollection.removeEntityById(id);
            }
          }));
        } else {

          // 
          this.repository.entityCollection.removeEntityById(id);
          this.repository.dataChangeHistory.addChange({ dataId: id, changeType: DataChangeType.Delete });
          return of(true);
        }
      })
    );
  }

  /**
   * 
   * @param id id
   */
  public removeAndSaveById(id: string) {
    const delete$ = this.apiProxy.deleteAndSave(id);
    const result$ = delete$.pipe(
      switchMap(() => {
        this.repository.entityCollection.removeEntityById(id);
        return of(true);
      })
    );

    return result$;
  }

  /**
   * 
   * @param id 
   */
  public removeByIds(ids: string[]): Observable<boolean> {

    // 
    const delete$ = this.apiProxy.extendBatchDelete(ids);

    // 
    return delete$.pipe(
      switchMap(() => {
        // 
        return this.applyChangesByIdArray(ids).pipe(
          tap(result => {
            if (result) {
              // 
              this.repository.entityCollection.removeEntities(
                (value) => {
                  return this.checkEntityValueExists(value, ids);
                }
              );
            }
          })
        );
      })
    );
  }

  /**
   * true
   * @param array 
   */
  private checkEntityValueExists(entity: Entity, primaryValueArray: any[]): boolean {
    let isExistInTargetArray = false;

    for (let i = 0; i < primaryValueArray.length; i++) {
      if (entity.primaryValue === primaryValueArray[i]) {
        isExistInTargetArray = true;
        break;
      }
    }

    return isExistInTargetArray;
  }

  /**
   * 
   * @param path 
   * @param id   
   */
  public removeByPath(fpath: string, id: string): Observable<boolean> {

    const delete$ = this.apiProxy.extendDeletByPath(fpath, id);

    // 
    return delete$.pipe(
      map(() => {
        this.repository.entityManager.removeEntityByPath(fpath, id);
        this.repository.dataChangeHistory.addChange({ fpath, dataId: id, changeType: DataChangeType.Delete });
        return true;
      })
    );
  }

  /**
   * id
   */
  public updateChangesById(id: string): Observable<boolean> {
    const entity = this.repository.entityCollection.getEntityById(id);

    // 
    if (!entity.changes || entity.changes.length === 0) {
      return of(true);
    }

    const changeDetail = this.repository.entityManager.buildEntityChangeDetailById(id);
    const update$ = this.apiProxy.update(changeDetail);
    const result$ = update$.pipe(
      tap(() => {
        this.repository.entityManager.clearEntityChangesById(id);
      }),
      map(() => {
        return true;
      })
    );
    return result$;
  }

  /**
   * 
   */
  public updateAllChanges(): Observable<boolean> {

    // 
    const updateResults: Observable<boolean>[] = [];
    const entities: Entity[] = this.repository.entityCollection.toArray();
    if (entities.length === 0) {
      return of(true);
    }

    entities.forEach((entity: Entity) => {
      const updateResult$ = this.updateChangesById(entity.primaryValue);
      updateResults.push(updateResult$);
    });

    // 
    const result$ = zip(...updateResults).pipe(
      map(() => {
        return true;
      })
    );

    return result$;
  }

  /**
   * 
   */
  public applyChanges(): Observable<boolean> {
    const save$ = this.apiProxy.save();
    const result$ = save$.pipe(
      tap(() => {
        this.repository.entityManager.clearAllEntityChanges();
        this.repository.dataChangeHistory.clear();
      }),
      map(() => {
        return true;
      })
    );

    return result$;
  }

  /**
   * idArray
   */
  public applyChangesByIdArray(ids: string[]): Observable<boolean> {
    const save$ = this.apiProxy.save();
    const result$ = save$.pipe(
      tap(() => {
        this.repository.entityManager.clearEntityChangesByIds(ids);
        this.repository.dataChangeHistory.clearByIds(ids);
      }),
      map(() => {
        return true;
      })
    );

    return result$;
  }

  /**
   * id
   */
  public applyChangesById(id: string): Observable<boolean> {
    const save$ = this.apiProxy.save();
    const result$ = save$.pipe(
      tap(() => {
        this.repository.entityManager.clearEntityChangesById(id);
        this.repository.dataChangeHistory.clearByIds([id]);
      }),
      map(() => {
        return true;
      })
    );

    return result$;
  }

  /**
   * 
   */
  public cancelChanges(): Observable<boolean> {
    const cancel$ = this.apiProxy.cancel();
    const result$ = cancel$.pipe(
      tap(() => {
        this.repository.entityManager.clearAllEntityChanges();
        this.repository.dataChangeHistory.clear();
      }),
      map(() => {
        return true;
      })
    );
    return result$;
  }

  /**
   * EntityFilter
   */
  private buildEntityFilter(filter: any[], sort: any[], pageSize: number, pageIndex: number) {
    const entityFilter = {
      FilterConditions: filter,
      SortConditions: sort,
      IsUsePagination: pageSize === 0 ? false : true,
      Pagination: {
        PageIndex: pageIndex,
        PageSize: pageSize,
        PageCount: 0,
        TotalCount: 0
      }
    };

    return entityFilter;
  }
}

export { BefDataService };
